import React from 'react';
import Crad from './Crad';

const Cardlist = ({ robort }) => {
    return (
        <div>
            {robort.map((user, i) => {
                return <Crad key={i} name={robort[i].name} email={robort[i].email} />
            })}
        </div>
    );
}
export default Cardlist;