import React, { Component } from 'react';
import Cardlist from '../components/cardlist';
import { robort } from '../robort';
import SearchBox from '../components/searchBox';
import Crad from '../components/Crad';
import ErrorBoundry from '../components/ErrorBoundry';
import Scroll from '../components/Scroll';
 
 


class App extends Component {
  constructor() {
    super()
    this.state = {
      robort: robort ,
      searchfield: ''

    }
  }
  compponentDidMount () {
    fetch('https://jsonplaceholder.typicode.com/users')
    .then(response => response.json())
    .then (users =>{this.setState({robort:users})})
  }
  onSearchChange = (event) => {
    this.setState({ searchfield: event.target.value })     
  }
  render() {
    const { robort , searchfield } =this.state
    const filterRobot = robort.filter(robot => {
      return robot.name.toLowerCase().includes(searchfield.toLowerCase());
    })
     return this.state.searchfield.length !==0 ?
       <div className='tc'>
           <h1>Robo Friends</h1>
          <SearchBox SearchCahneg={this.onSearchChange} /> 
          <Scroll>
            <Crad name={searchfield} />
          </Scroll>   
        </div>
        :
        (
        <div className='tc'>
          <h1>Robo Friends</h1>
          <SearchBox SearchCahneg={this.onSearchChange} />
          <Scroll>
            <ErrorBoundry>
               <Cardlist robort={filterRobot} />
             </ErrorBoundry>
          </Scroll>
        </div>
    );
    }
}
export default App;