import React from 'react';

const SearchBox = ({SearchCahneg}) => {
    return (
        <div className='pa2'>
            <input
                className='pa3 ba b--green bg-lightest-blue' 
                type='search' 
                placeholder='search Robots' 
                onChange={SearchCahneg} />
                
        </div>
    );
}


export default SearchBox;