import React from 'react';


const Crad = ({name ,email}) =>{
    return (
        <div className='bg-light-green dark-red dib br3 pa3 ma2 grow dim bw2 shadow-1 tc'>
            <img alt='robots' src={`https://robohash.org/${name}?size=200x200`}/>
            <div>
                <h2>{name}</h2>
                <p>{email}</p>
            </div >
        </div>
    );
}
export default Crad ;